const theme = createMuiTheme({
  typography: {
    useNextVariants: true,
  }
});

export default class App extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      coords: [],
      value: 0,
      location: "Current",
      weather: [],
      forecast: [],
      open: true,
      geoSupport: false,
      loading: false
    };
  }

  componentDidMount = () => {
    console.log("componentDidMount")

    // this.navigatorPreCheck();
    // this.checkWeather();
  }

  navigatorPreCheck = () => {
    console.log("navigatorPreCheck");
    
    // if ("geolocation" in navigator) {
    //   /* geolocation is available */
    //         console.log("granted");
    // } else {
    //   /* geolocation IS NOT available */
    //         console.log("prompt");
    // }

    if (navigator.geolocation) {
        // navigator.permissions.query(this.logMe);
        navigator.permissions.query({name:'geolocation'}).then((result) => {
          if (result.state === 'granted') {
            console.log("granted");
            this.setState({
              open: false,
              geoSupport: true
            }, () => {
               // console.log("here?");
            });
          } else if (result.state === 'prompt') {
            console.log("prompt");
          }

          // this.setState({
          //   open: true,
          //   geoSupport: false
          // });
          console.log("denied");
          this.setState({
            loading: true,
          });
          // Don't do anything if the permission was denied.
        });
        navigator.geolocation.getCurrentPosition(this.checkWeather);
        navigator.geolocation.getCurrentPosition(this.checkForecast);

    } else { 
      console.log("else");
      console.log("Geolocation is not supported by your browser.")
    };
  }

  logMe = (log) => {
    console.log("log:", log);
  }


	checkWeather = async (position) => {
    // let position = {latitude: 44.802048,longitude: 20.457062399999998};
    // console.warn("checkWeather")
    try {
      let res = await UserApi.weather({
        lat: position.coords.latitude,
        lon: position.coords.longitude
        // lat: position.latitude,
        // lon: position.longitude
      });
      if (res.error) {
        console.error(res.error);
        this.setState({
          loading: true,
        });
        return;
      } else {
        // console.log("checkWeather", res);
        this.setState({
          weather: res,
          geoSupport: true,
          loading: false,
        }); 
      }
    } catch(e) {
      console.error(e);
      return;
    }
  }

  checkForecast = async (position) => {
    // console.warn("checkForecast")
    try {
      let res = await UserApi.forecast({
        lat: position.coords.latitude,
        lon: position.coords.longitude,
        units: "metric",
      });
      if (res.error) {
        console.error(res.error);
        this.setState({
          loading: true,
        });
        return;
      } else {
        // console.log("checkForecast", res);
        this.setState({
          forecast: res,
          loading: false,
        }); 
      }
    } catch(e) {
      console.error(e);
      return;
    }
  }

  handleTabChange = (event, value) => {
    this.setState({value});
  }


  handleClickOpen = () => {
    this.setState({ open: true });
  }

  handleClose = () => {
    this.setState({ open: false });
  }

	render() {
    const { geoSupport, loading, location, value } = this.state;

		return (
      <MuiThemeProvider theme={theme}>
        <div className="appContainer">
          {!geoSupport &&
            <Dialog
              open={this.state.open}
              onClose={this.handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Please turn on your GPS location."}</DialogTitle>
              <DialogActions>
                <Button onClick={this.handleClose} color="primary">
                  Disagree
                </Button>
                <Button onClick={this.navigatorPreCheck} color="primary" autoFocus>
                  Agree
                </Button>
              </DialogActions>
            </Dialog>
          }

          {loading  &&
            <CircularDeterminate/>
          }

          {geoSupport &&
      			<div>
              <AppBar position="absolute">
              {/*
                <Tabs value={value} onChange={() => this.handleChange(value)} fullWidth>
              */}
                <Tabs value={value} onChange={this.handleTabChange} fullWidth>
                  <Tab label={location}  className="" style={{backgroundColor: "purple"}}/>
                  <Tab label="Forecast" className=""  style={{backgroundColor: "purple"}}/>
                  <Tab label="Search" className="" style={{backgroundColor: "purple"}} />
                </Tabs>
              </AppBar>
              {value === 0 &&
                <TabContainer>
                  <Current 
                    weather={this.state.weather && this.state.weather}
                  />

                </TabContainer>
              }
              {value === 1 &&
                <TabContainer>
                  <Forecast 
                    forecast={this.state.forecast && this.state.forecast}
                  />
                </TabContainer>
              }
              {value === 2 &&
                <TabContainer>
                  <Search />
                </TabContainer>
              }


              {/*

      				<h1>Small step for a big weather app</h1>
              <p> 
                coords: &nbsp;
                {this.state.coords.lat}, {this.state.coords.lon}
              </p>
              <p> 
                city: &nbsp;
                {this.state.city}
              </p>
              <Button variant="contained" color="primary">Click me</Button>
              */}
      			</div>
          }
        </div>
      </MuiThemeProvider>

		);
	}
}

