const axios = require('axios');

// export const apiUrl = process.env.NODE_ENV === 'development' ? 'http://192.168.0.50:5000' : location.origin;

class Api {
  // static baseApi = apiUrl

  constructor() {}
}

export class UserApi extends Api {
  constructor() {
    super();
  }

  static weather = (data) => {
    // console.log("weather", data)
    // let data = {lat: 51.5074, lon: 0.1278};
    let url = `https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/weather?lat=${data.lat}&lon=${data.lon}&units=metric&APPID=7e78e10a4f0e6201b7fe87baad534ac4`
    // let url = 'http://api.openweathermap.org/data/2.5/forecast?id=524901&APPID=7e78e10a4f0e6201b7fe87baad534ac4'

    return axios.get(url, data)
      .then((res) => res.data)
      .catch((e) => {
        console.error(e);
      });
  };

  static forecast = (data) => {
    // console.log("weather", data)
    // let data = {lat: 51.5074, lon: 0.1278};
    let url = `https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/forecast?lat=${data.lat}&lon=${data.lon}&units=${data.units}&APPID=7e78e10a4f0e6201b7fe87baad534ac4`
    // let url = 'http://api.openweathermap.org/data/2.5/forecast?id=524901&APPID=7e78e10a4f0e6201b7fe87baad534ac4'

    return axios.get(url, data)
      .then((res) => res.data)
      .catch((e) => {
        console.error(e);
      });
  };

}
